<template>
	<view class="body">
		<view class="brushDate">下拉刷新最新状态~</view>
		<view class="cell" v-for="(item,index) in data" :key="index">
			<view class="icons" v-show="item.type=='correction'">批改</view>
			<view class="icons" v-show="item.type=='model'">模板</view>
			<view class="list_cell">
			<!-- <view class="list_cell" @click="toPdfList(item)"> -->
				<view class="list_left">{{item.goods_sub_title || "商品名称"}}<view v-if="item.is_group=='1'">(拼团价)</view></view>
				<view class="list_right">
					<view class="text">支付金额</view>
					<view class="icon">￥</view>
					<view class="price">{{item.group_price || item.money || 0}}</view>
				</view>
			</view>
			<view class="cell_status">
				<view class="time">提交时间:{{item.create_time}}</view>
			</view>
			<view class="refund" v-show="item.state==2">已退款</view>
			<view class="cell_type" v-show="item.state!=2">
				<view :class="{boxs:true,checked:item.state==0}">待付款</view>
				<img src="../../static/mine/arrow_icon.png">
				<view :class="{boxs:true,checked:item.state==1 && item.evaluate==null}">已付款</view>
				<img src="../../static/mine/arrow_icon.png">
				<view :class="{boxs:true,checked:item.evaluate!=null}">评价</view>
			</view>
			<view class="buttons">
				<view v-show="item.state==0" class="payNow" @click="todelOrder(item)">取消订单</view>
				<view v-show="item.state==0" class="payNow" @click="toPayfor(item)">立即付款</view>
			</view>
			<view v-show="item.state=='1' && item.evaluate==null" class="comment" @click="toComment(item)">点击评价</view>
			<view class="comments" v-if="item.evaluate!=null && item.evaluate.text!=''">我的评价: {{item.evaluate.text}}</view>
			<img class="user_img" v-if="item.evaluate!=null && item.evaluate.img!=''" :src="item.evaluate.img" mode="aspectFit" @click="preview(item.evaluate.img)">
		</view>
		<view v-if="data.length<=0" class="noGoods">暂无商品哦~</view>
		<view class="module" v-if="show">
			<view class="models">
				<view class="title">确认删除该订单吗？</view>
				<view class="buttons">
					<view class="cancel" @click="show=false">取消</view>
					<view class="sure" @click="sureDel">确定</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		order, toBePay, delOrder
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				token:'',
				data:[],
				pages:1,
				status:'',
				id:'', //删除订单的id
				show:false, //删除订单确认框
				allPages:''
			}
		},
		// 滑动到页面底部加载下一页
		onReachBottom(){
			// 判断是否有数据
			if(this.pages>=this.allPages){
				this.$u.toast("暂无更多")
			}else{
				this.pages++
				console.log(this.pages)
				this.resetNow();
			}
			// this.getData(); // 获取数据
			console.log('我滚动到底部了')
		},
		onPullDownRefresh(){
			setTimeout(()=>{
				this.data = []
				this.resetNow();
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onLoad(option) {
			this.token = store.state.vuex_token
			this.id = option.id
			this.status = option.status
			console.log(option.status)
		},
		onShow() {
			this.data = []
			this.resetNow();
		},
		methods: {
			// 重新请求当前页面
			resetNow() {
				if(this.status!=undefined){
					if(this.status==1){
						uni.setNavigationBarTitle({
							title: '待付款'
						});
						order({token:this.token,page:this.pages, state:'0'}).then(res=>{
							console.log(res)
							this.data.push(...res.data.data.data)
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					}else if(this.status==2){
						uni.setNavigationBarTitle({
							title: '已付款'
						});
						order({token:this.token,page:this.pages, state:'1'}).then(res=>{
							console.log(res)
							this.data.push(...res.data.data.data)
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					}else{
						uni.setNavigationBarTitle({
							title: '评价'
						});
						order({token:this.token,page:this.pages, state:'evaluate'}).then(res=>{
							console.log(res)
							this.data.push(...res.data.data.data)
							this.allPages = res.data.data.last_page
						}).catch(err=>{
							console.log(err)
						})
					}
				}else{
					console.log(this.status,'this.status不存在')
					uni.setNavigationBarTitle({
						title: '全部订单'
					});
					order({token:this.token, page:this.pages}).then(res=>{
						console.log(res)
						this.data.push(...res.data.data.data)
						this.allPages = res.data.data.last_page
					}).catch(err=>{
						console.log(err)
					})
				}
			},
			// 去评价
			toComment(e) {
				uni.navigateTo({
					url: '/pages/mine/comment?id='+e.id
				})
			},
			// 去支付列表
			toPdfList(e) {
				uni.navigateTo({
					url: '/pages/index/pdfList?id=' + e.unit_id + '&title=' + e.one_title
				})
			},
			// 预览图片
			preview(e) {
				let file = []
				file.push(e)
				uni.previewImage({
					urls: file
				});
			},
			todelOrder(e) {
				this.show = true
				this.id = e.id
				console.log('this.id:',this.id)
			},
			// 删除订单
			sureDel() {
				delOrder({token:this.token,order_id: this.id,}).then(res => {
					console.log(res)
					this.$u.toast(res.data.data)
					if(res.data.data!=-1){
						this.data = []
						this.resetNow();
						this.show = false
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 立即支付 拉起支付
			toPayfor(e) {
				let that = this
				toBePay({
					token:this.token,
					order_id: e.id,
				}).then(res => {
					console.log(res)
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: String(res.data.data.timeStamp),
						nonceStr: res.data.data.nonceStr,
						package: 'prepay_id='+res.data.data.prepay_id,
						signType: 'MD5',
						paySign: res.data.data.sign,
					    success: function (res) {
							console.log(res);
							console.log("支付成功");
							that.$u.toast("购买成功")
							that.data = []
							setTimeout(()=>{
								that.resetNow();
							}, 1000);
					    },
					    fail: function (err) {
					        console.log('支付失败:' + JSON.stringify(err));
					    }
					});
				}).catch(err => {
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100%;
		min-height: 100vh;
		padding-top: 1rpx;
		background-color: #f7f7fb;
		.buttons{
			display: flex;
			justify-content: space-between;
		}
		.user_img{
			width: 100%;
			height: 300rpx;
			margin-top: 20rpx;
		}
		.brushDate{
			font-size: 22rpx;
			text-align: center;
			margin: 10rpx 0;
			color: #666;
		}
		.noGoods{
			font-size: 28rpx;
			text-align: center;
			margin-top: 10rpx;
		}
		.cell{
			width: 82%;
			padding: 20rpx 4%;
			background-color: #fff;
			border-radius: 20rpx;
			margin: 30rpx 5%;
			position: relative;
			overflow: hidden;
			.refund{
				margin-bottom: 20rpx;
				color: #ee2000;
				font-size: 30rpx;
			}
			.icons{
				right: 0;
				top: 0;
				color: #fff;
				font-size: 28rpx;
				position: absolute;
				padding: 8rpx 20rpx;
				border-bottom-left-radius: 20rpx;
				background-color: #75b9ff;
			}
			.comments{
				font-size: 28rpx;
				border: 1rpx solid #666;
				border-radius: 10rpx;
				padding: 10rpx 20rpx;
				margin-top: 20rpx;
			}
			.list_cell{
				display: flex;
				justify-content: space-between;
				border-bottom: 1px solid #f7f7fb;
				.list_left{
					width: 300rpx;
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
					font-size: 28rpx;
					padding: 20rpx 0;
					padding-left: 60rpx;
					background-image: url(../../static/mine/car_icon.png);
					background-size: 40rpx;
					background-repeat: no-repeat;
					background-position: left center;
					view{
						display: inline-block;
						color: #ff6666;
						font-size: 24rpx;
						font-weight: 500;
					}
				}
				.list_right{
					display: flex;
					margin-top: 10rpx;
					align-items: baseline;
					.text{
						font-size: 28rpx;
						margin-right: 10rpx;
					}
					.icon{
						font-size: 20rpx;
					}
					.price{
						color: red;
						font-size: 38rpx;
					}
				}
			}
			.cell_status{
				margin: 20rpx 0;
				.time{
					font-size: 22rpx;
					color: #717171;
				}
			}
			.cell_type{
				display: flex;
				justify-content: space-between;
				align-items: center;
				.boxs{
					border: 1rpx solid #000;
					width: 20%;
					text-align: center;
					font-size: 28rpx;
					line-height: 60rpx;
					border-radius: 10rpx;
				}
				.checked{
					background-color: #75b9ff;
					color: #fff;
					border: 1px solid #75b9ff;
				}
				img{
					width: 18%;
					height: 10rpx;
					margin: 0 6rpx;
				}
			}
			.payNow,.comment{
				width: 200rpx;
				font-size: 24rpx;
				color: #fff;
				padding: 12rpx 0;
				border-radius: 50rpx;
				text-align: center;
				margin: 20rpx auto;
			}
			.payNow{
				background-color: #e00000;
			}
			.comment{
				background-color: #75b9ff;
			}
		}
		
		.module{
			width: 100vw;
			height: 100vh;
			position: fixed;
			top: 0;
			right: 0;
			z-index: 999;
			background: rgba(0, 0, 0, .3);
			display: flex;
			justify-content: center;
			align-items: center;
			.models{
				background-color: #fff;
				border-radius: 20rpx;
				width: 56%;
				.title{
					font-size: 32rpx;
					font-weight: 500;
					text-align: center;
					padding: 40rpx 0;
					margin-bottom: 20rpx;
				}
				.buttons{
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;
					view{
						line-height: 78rpx;
						font-size: 30rpx;
						width: 50%;
						text-align: center;
					}
					.cancel{
						background-color: #eee;
						border-bottom-left-radius: 20rpx;
						border: 1rpx solid #eee;
					}
					.sure{
						background-color: #999;
						border-bottom-right-radius: 20rpx;
						border: 1rpx solid #999;
					}
				}
			}
		}
	}
</style>